.controls {
    margin: 0;
    padding: 0;
    background-color: #062B55;
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
}

.controls button,
.controls input {
    width: 100px;
    height: 50px;
    background-color: blue;
    margin-right: 20px;
    margin-left: 20px;
}

.controls input {
    width: 250px;
    background-color: #ffffff;
}

.controls:last-child {
    margin: 0;
}